body {
  min-width: 320px;
  margin: 0;
  box-sizing: border-box;
  font-family: RobotoCondensed, Arial, sans-serif;
  font-style: normal;
  font-weight: normal; }

.d-none {
  display: none; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

*,
*:after,
*:before {
  box-sizing: inherit; }

ul {
  padding: 0;
  margin: 0;
  list-style: none; }

img {
  display: block;
  height: auto; }

a {
  color: inherit;
  text-decoration: none; }

.js-object {
  cursor: pointer; }

@font-face {
  font-family: RobotoCondensed;
  src: url("../fonts/RobotoCondensed-Regular.ttf") format("ttf");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: RobotoCondensed;
  src: url("../fonts/RobotoCondensed-Light.ttf") format("ttf");
  font-style: normal;
  font-weight: 300; }

@font-face {
  font-family: RobotoCondensed;
  src: url("../fonts/RobotoCondensed-Bold.ttf") format("ttf");
  font-style: normal;
  font-weight: 700; }

@font-face {
  font-family: RobotoCondensed;
  src: url("../fonts/RobotoCondensed-LightItalic.ttf") format("ttf");
  font-style: italic;
  font-weight: 300; }

@font-face {
  font-family: RobotoCondensed;
  src: url("../fonts/RobotoCondensed-BoldItalic.ttf") format("ttf");
  font-style: italic;
  font-weight: 700; }

.title--h2 {
  font-size: 24px; }

.title--h3 {
  font-size: 21px; }

.title--h4 {
  font-size: 18px; }

.map {
  width: 100%;
  overflow: auto; }

.map-image__object {
  transition-duration: 200ms; }
  .map-image__object--learning {
    fill: #3bd11d; }
    .map-image__object--learning:hover {
      fill: #227711; }
  .map-image__object--admin {
    fill: #fce703; }
    .map-image__object--admin:hover {
      fill: #978b02; }
  .map-image__object--sport {
    fill: #036ffc; }
    .map-image__object--sport:hover {
      fill: #024397; }
  .map-image__object--living {
    fill: #e4660c; }
    .map-image__object--living:hover {
      fill: #833b07; }
  .map-image__object--culture {
    fill: #834011; }
    .map-image__object--culture:hover {
      fill: #291405; }
  .map-image__object--extra {
    fill: none;
    stroke: #3bd11d;
    stroke-width: 5px; }
    .map-image__object--extra:hover {
      fill: none;
      stroke: #227711; }
  .map-image__object--neutral {
    fill: #827f84 !important; }
  .map-image__object--neutral-extra {
    fill: none !important;
    stroke: #827f84 !important; }

.map-image__polygon {
  opacity: 0; }

.map-image {
  height: 100vh; }

@media (min-width: 1200px) {
  .map-image {
    width: 100%;
    height: auto; } }

.btn {
  background: none;
  border: none; }
  .btn--base {
    border: 1px solid #828282; }
  .btn--menu {
    position: fixed;
    top: 10px;
    left: 10px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: #fffffa;
    border: 1px solid #828282; }

@media (min-width: 1200px) {
  .btn--menu {
    display: none; } }

.popup {
  position: absolute;
  padding: 10px; }
  .popup--hidden {
    display: none; }

.popup-btn {
  position: absolute;
  right: 0; }

.info {
  width: 95%;
  height: 95vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 14px;
  background-color: #ffffff; }

.info-name {
  max-width: 75%;
  margin-bottom: 10px; }

.info-null {
  margin: auto;
  width: fit-content;
  color: #b30435;
  font-size: 18px;
  font-weight: 600; }

.info__tabs-link:hover {
  color: #3a7746; }

.info__tabs-content {
  overflow-y: scroll;
  max-height: 69%; }

.info-title {
  color: #3a7746; }

.info-separator {
  padding: 8px 0;
  padding-left: 5px; }
  .info-separator:not(:last-child) {
    border-bottom: 1px solid #3a7746; }

.info__rooms-list {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 5px; }

.info__rooms-list-item {
  display: block;
  padding: 3px;
  border: 1px solid #3a7746;
  margin: 0;
  margin-bottom: 5px;
  margin-right: 10px; }
  .info__rooms-list-item:first-child {
    margin-left: 0; }
  .info__rooms-list-item:last-child {
    margin-right: 0; }

@media (min-width: 720px) {
  .info {
    width: 33%;
    position: absolute;
    transform: none;
    height: auto; }
  .info__tabs-content {
    max-height: 400px; } }

.building-title {
  padding: 7px;
  border-radius: 4px;
  color: #ffffff; }
  .building-title p {
    margin-bottom: 0; }
  .building-title--learning {
    background: #227711; }
  .building-title--admin {
    background: #978b02; }
  .building-title--sport {
    background: #024397; }
  .building-title--leaving {
    background: #833b07; }
  .building-title--culture {
    background: #291405; }

.deanery-floor {
  font-size: 14px; }

.legend__btn {
  position: fixed;
  display: block;
  top: 10px;
  left: 10px; }

.legend {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 15px;
  position: fixed;
  transition-duration: 450ms;
  top: 10px;
  left: -100%; }
  .legend--showed {
    left: 10px; }

.legend__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.legend__header-title {
  max-width: 60%; }

.legend__header-close {
  width: 30px;
  height: 30px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.legend__type-list-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 10px; }

@media (min-width: 1200px) {
  .legend__btn {
    display: none; }
  .legend {
    left: 10px; } }

.building-type-color {
  width: 30px;
  height: 20px;
  margin-right: 10px;
  cursor: pointer;
  border: none; }
  .building-type-color--learning {
    background-color: #3bd11d; }
    .building-type-color--learning:hover, .building-type-color--learning:active, .building-type-color--learning:focus {
      box-shadow: 0 0 10px #227711; }
  .building-type-color--admin {
    background-color: #fce703; }
    .building-type-color--admin:hover, .building-type-color--admin:active, .building-type-color--admin:focus {
      box-shadow: 0 0 10px #978b02; }
  .building-type-color--sport {
    background-color: #036ffc; }
    .building-type-color--sport:hover, .building-type-color--sport:active, .building-type-color--sport:focus {
      box-shadow: 0 0 10px #024397; }
  .building-type-color--leaving {
    background-color: #e4660c; }
    .building-type-color--leaving:hover, .building-type-color--leaving:active, .building-type-color--leaving:focus {
      box-shadow: 0 0 10px #833b07; }
  .building-type-color--culture {
    background-color: #834011; }
    .building-type-color--culture:hover, .building-type-color--culture:active, .building-type-color--culture:focus {
      box-shadow: 0 0 10px #291405; }
